import {
  Chart,
  ChartSeries,
  ChartSeriesItem,
  ChartValueAxis,
  ChartValueAxisItem,
  ChartCategoryAxis,
  ChartCategoryAxisItem,
  ChartTitle,
  ChartLegend,
} from "@progress/kendo-react-charts";
import { COLORS } from './constants'
import React from "react";
import ReactDOM from "react-dom";
import App from "../App";
let categories = [];


class FlavorForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {value: 'coconut'};

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
      changeCategory(event.target.value);
      this.setState({value: event.target.value});
  }

  handleSubmit(event) {
      event.preventDefault();
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <label>
          Километраж по :
          <select value={this.state.value} onChange={this.handleChange} id='select'>
            <option value="months">месяцам</option>
            <option value="days">дням</option>
            <option value="weeks">неделям</option>
          </select>
        </label>
        {/*<input type="submit" value="Выбрать" id="submit"/>*/}
      </form>
    );
  }
}


export const series = [
  {
 name: "месяц",
 data: [400, 358, 418, 425],
 color: COLORS.total,
  }
];


const changeCategory = function(category){
    if (category === 'days'){
        categories = []
        const request = new XMLHttpRequest();
        request.open('GET', 'http://127.0.0.1:8000/main/getData/', false);
        request.setRequestHeader('Access-Control-Allow-Headers', '*')
        request.send();
        let result = ''
        request.addEventListener('load', function() {
            console.log('a');
            if (request.status === 200) {
                console.log('b');
                result = request.responseText;}
            })
        console.log(result);
        result = parseInt(result);
        for (let i=0; i < result; i++){
            categories.push(i);
        }
        data = categories.map(function(item){
            return item*item
        });
        series[0]['data'] = data;
        console.log(data, result);
        ReactDOM.render(<React.StrictMode>
            <App />
          </React.StrictMode>,
          document.getElementById('root'))
            }
}


let data = categories.map(function(item){
    return item*item
});

series[0]['data'] = data;
let Line = props => {
  return (
 <Chart pannable zoomable style={{ height: 350 }}>
 <ChartTitle text="Статистка" />
 <ChartLegend position="top" orientation="horizontal" />
 <ChartValueAxis>
 <ChartValueAxisItem title={{ text: "километры" }} min={0} max={500} />
 </ChartValueAxis>
 <ChartCategoryAxis>
 <ChartCategoryAxisItem categories={categories} />
 </ChartCategoryAxis>
 <ChartSeries>
 {series.map((item, idx) => (
 <ChartSeriesItem
 key={idx}
 type="line"
 tooltip={{ visible: true }}
 data={item.data}
 name={item.name}
 />
 ))}
 </ChartSeries>
 </Chart>
  );
};

export {FlavorForm, Line};
